.profile {
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .profile_cover {
    width: 100%;
    max-width: 100vw;
    height: 300px;
    padding: 0 15%;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.1)
      ),
      url("https://source.unsplash.com/random/?nature");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: brown;

    .profile_coverImg {
      height: 200px;
      width: 200px;
      object-fit: cover;
      margin-right: 50px;
    }

    .profile_coverInfo {
      .profile_coverName {
        color: #fff;
        font-family: "Bebas Neue", cursive;
        font-size: clamp(1.8rem, 3vw, 4rem);
      }

      .profile_coverInfo {
        color: #fff;
        font-family: "Roboto", sans-serif;
        font-size: clamp(1rem, 2vw, 1.4rem);
      }
    }
  }

  .profile_info {
    width: 70%;
    min-width: 450px;
    margin: 30px auto;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.4);

    .profile_infoAbout {
      margin-bottom: 20px;
    }
  }
  .profile_impressions {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
@media only screen and(max-width: 480px) {
  .profile {
    .profile_cover {
      flex-direction: column;
      padding: 0;

      .profile_coverImg {
        width: 150px;
        height: 150px;
        margin: 20px 0;
      }

      .profile_coverInfo {
        text-align: center;
      }
    }
    .profile_info {
      width: 90%;
      min-width: 200px;
      margin: 30px auto;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.4);
    }
  }
}
